.questions {
  margin-top: 120px;
}

.questions h3 {
  text-align: center;
  font-family: $font-family-mono;
  font-weight: 700;
  font-size: $font-size--h3 + 12;
  line-height: $font-size--h3 + 22;
  color: $text-color;
  letter-spacing: 0.03em;
  margin-top: 0;
  margin-bottom: 49px;
}

.questions__list {
  font-family: $font-family;
  list-style: none;
  margin: 0 auto;
  padding: 0;
  color: $text-color;
  width: 82.9%;
  margin-bottom: 108px;
}

.questions__list-text {
  display: none;
}

.questions__list-item {
  border: 1px solid #a87b62;
  padding-left: 43px;
  min-height: 112px;
  margin-top: -1px;
  position: relative;
  cursor: pointer;
}

.questions__list-item br {
  display: none;
}

.questions__list-item h4 {
  font-weight: 500;
  font-size: $font-size + 2;
  line-height: $font-size + 7;
  margin-top: 45px;
  margin-bottom: -8px;
}

.questions__list-item p {
  font-weight: 400;
  font-size: $font-size;
  line-height: $font-size + 5;
  width: 57%;
  margin-bottom: 41px;
  padding-left: 5px;
}

