.accordion__toggle {
  width: 15px;
  height: 15px;
  position: absolute;
  content: "";
  left: calc(100% - 62px);
  top: 47px;
  border: none;
  background: url("../img/arrow_up.svg") center no-repeat;
  cursor: pointer;
  font-size: 0;

  &--inactive {
    background: url("../img/arrow_down.svg") center no-repeat;
  }
}

.accordion__pane--hidden {
  display: none;
}

@media (max-width: 1023px) {
  .questions {
    margin-top: 70px;
  }

  .questions__list {
    width: 100%;
    margin-bottom: 74px;
  }

  // .questions__button {
    // width: 23px;
    // height: 12px;
    // background: url("../img/arrow_down_mobile.svg") center no-repeat;
    // left: calc(100% - 72px);
  // }

  // .questions__button--active {
    // background: url("../img/arrow__up_mobile.svg") center no-repeat;
  // }

  .questions__list-item {
    padding-left: 48px;
  }

  .questions__list-item p {
    margin-top: 34px;
    margin-bottom: 44px;
    width: 85%;
    padding-left: 0;
  }

  .accordion__toggle {
    width: 23px;
    height: 12px;
    background: url("../img/arrow_up_mobile.svg") center no-repeat;
    left: calc(100% - 72px);

    &--inactive {
      background: url("../img/arrow_down_mobile.svg") center no-repeat;
    }
  }
}

@media (max-width: 767px) {
  .questions {
    margin-top: 60px;
  }

  .questions h3 {
    margin-bottom: 30px;
  }

  .questions__list {
    margin-bottom: 45px;
  }

  .questions__button {
    top: 22px;
    left: calc(100% - 45px);
  }

  .questions__list-item {
    padding-left: 25px;
    min-height: 83px;
  }

  .questions__list-item br {
    display: block;
  }

  .questions__list-item h4 {
    width: 70%;
    font-size: 16px;
    line-height: 22px;
    margin-top: 15px;
    margin-bottom: 10px;
  }

  .questions__list-item:nth-child(3n+2) h4 {
    width: 58%;
  }

  .questions__list-item:nth-child(3n+3) h4 {
    width: 64%;
  }

  .questions__list-item p {
    margin-bottom: 23px;
    font-size: $font-size--middle;
    line-height: $font-size--middle + 6;
    width: 78%;
    margin-top: 12px;
  }
}
